import React, { Component } from 'react';
import { ButtonToolbar, ButtonGroup, Button, Form, FormGroup, InputGroup, FormControl, Col, Table, Badge} from 'react-bootstrap';
import { faPencilAlt,  faTrashAlt, faPlusSquare,  faSearch, faCopy, faSync, faGripVertical} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {ComboBox, FeedbackCtrl, DataGrid, Modal} from '../libs/components/Components';
import {$glVars} from '../common/common';
import { JsNx, UtilsString } from '../libs/utils/Utils';
import { Pagination } from '../libs/components/Pagination';

export class TemplatesView extends Component{
    constructor(props){
        super(props);
        
        this.onClose = this.onClose.bind(this);
        this.getData = this.getData.bind(this);
        this.getDataResult = this.getDataResult.bind(this);
        this.onClone = this.onClone.bind(this);

        this.state = {dataProvider: [], templateId: -1, queryStr: "", pagination: {current_page: 1, count: 0, item_per_page: 25}};
    }

    componentDidMount(){
      //  $glVars.webApi.addObserver("TemplatesView", this.getData, ['saveTemplate']);        
        this.getData();
    }

    componentWillUnmount(){
      //  $glVars.webApi.removeObserver("TemplatesView");
    }

    componentDidUpdate(prevProps){
        /*if(prevProps.userId !== this.props.userId){
            this.getData();
        }*/
    }

    getData(){
        $glVars.webApi.getTemplateList(this.state.pagination.item_per_page, this.state.pagination.current_page - 1, this.getDataResult);
    }

    getDataResult(result){
        if(!result.success){
            FeedbackCtrl.instance.showError($glVars.i18n.tags.appName, result.msg);
            return;
        }

        let pagination = this.state.pagination;
        pagination.current_page = parseInt(result.data.current_offset) + 1; 
        pagination.count = parseInt(result.data.total_count);
        this.setState({dataProvider: result.data.items, templateId: -1, pagination: pagination});
    }

    changePage(page){
        let pagination = this.state.pagination;
        pagination.current_page = page
        this.setState({pagination: pagination}, this.getData);
    }

    render(){
        let dataProvider = this.state.dataProvider;
        let regexp = UtilsString.getRegExp(this.state.queryStr);
        let that = this;

        if(this.state.queryStr.length > 0){
            
            dataProvider = dataProvider.filter(function(item){
                let categories = that.getCategories(item);
                if((item.name.search(regexp) >= 0) || (item.description.search(regexp) >= 0) || (categories.search(regexp) >= 0)){
                    return true;
                }
                else{
                    return false;
                }
            })
        }

        let main = 
            <div>
                <ButtonToolbar className="mb-4 justify-content-end">
                    <ButtonGroup className="mr-1">
                        <Button  title="Ajouter" onClick={() => this.setState({templateId: 0})} variant="primary"><FontAwesomeIcon icon={faPlusSquare}/>{" Crer un gabarit"}</Button>
                    </ButtonGroup>
                    <ButtonGroup >
                        <Button  title="Actualiser" onClick={() => this.getData()} variant="primary"><FontAwesomeIcon icon={faSync}/></Button>
                    </ButtonGroup>
                </ButtonToolbar>

                <FormGroup>
                    <InputGroup>
                        <FormControl autoFocus type="text" placeholder={"Recherchez..."} onChange={(event) => this.setState({queryStr: event.target.value})} value={this.state.queryStr}  aria-describedby="inputGroupPrepend" />
                        <InputGroup.Prepend>
                            <InputGroup.Text id="inputGroupPrepend"><FontAwesomeIcon icon={faSearch}/></InputGroup.Text>
                        </InputGroup.Prepend>
                    </InputGroup>
                </FormGroup>

                <DataGrid orderBy={true}>
                    <DataGrid.Header>
                        <DataGrid.Header.Row>
                            <DataGrid.Header.Cell style={{width: 80}}>{"#"}</DataGrid.Header.Cell>
                            <DataGrid.Header.Cell >{"Catgorie(s)"}</DataGrid.Header.Cell>
                            <DataGrid.Header.Cell >{"Nom"}</DataGrid.Header.Cell>
                            <DataGrid.Header.Cell >{"Description"}</DataGrid.Header.Cell>
                            <DataGrid.Header.Cell style={{width: 100}}>{}</DataGrid.Header.Cell>
                        </DataGrid.Header.Row>
                    </DataGrid.Header>
                    <DataGrid.Body>
                        {dataProvider.map((item, index) => {
                                let row = 
                                    <DataGrid.Body.Row key={index}>
                                        <DataGrid.Body.Cell>{(this.state.pagination.item_per_page * (this.state.pagination.current_page-1)) + index + 1}</DataGrid.Body.Cell>
                                        <DataGrid.Body.Cell>{this.getCategories(item)}</DataGrid.Body.Cell>
                                        <DataGrid.Body.Cell>{item.name}</DataGrid.Body.Cell>
                                        <DataGrid.Body.Cell>{item.description}</DataGrid.Body.Cell>
                                        <DataGrid.Body.Cell style={{textAlign: 'center'}}>
                                            <ButtonGroup size="sm">
                                                <Button title="diter" onClick={() => this.setState({templateId: item.id})} variant="primary"><FontAwesomeIcon icon={faPencilAlt}/></Button>
                                                <Button title="Copier" onClick={() => this.onClone(item.id)} variant="primary"><FontAwesomeIcon icon={faCopy}/></Button>
                                                <Button title="Supprimer" onClick={() =>  this.onRemove(item.id)} variant="primary"><FontAwesomeIcon icon={faTrashAlt}/></Button>
                                            </ButtonGroup>
                                        </DataGrid.Body.Cell>
                                    </DataGrid.Body.Row>
                                return (row);                                    
                            }
                        )}
                    </DataGrid.Body>
                </DataGrid>
                <Pagination pagination={this.state.pagination} onChangePage={(p) => this.changePage(p)}/>
                {this.state.templateId >= 0 && <ModalTemplateForm templateId={this.state.templateId} title={this.state.templateId == 0 ? 'Crer un gabarit' : 'Modifer un gabarit'} onClose={this.onClose}/>}
            </div>;

        return main;
    }

    getCategories(item){
        let result = [];
        for(let act of item.activities){
            result.push(act.categoryName);
        }
        result = [...new Set(result)]; // distinct values
        return result.join(", ");
    }

    onClose(refresh){
        if(refresh){
            this.getData();
        }
        else{
            this.setState({templateId: -1});
        }
    }

    onRemove(templateId){
        let that = this;
        let callback = function(result){
            if(!result.success){
                FeedbackCtrl.instance.showError($glVars.i18n.tags.appName, result.msg);
            }
            else{
                FeedbackCtrl.instance.showInfo($glVars.i18n.tags.appName, $glVars.i18n.tags.msgSuccess, 3);
                that.getData();
            }
        };

        if(window.confirm($glVars.i18n.tags.msgConfirmDeletion)){
            $glVars.webApi.deleteTemplate(templateId, callback);
        }
    }

    onClone(templateId){
        let that = this;
        let callback = function(result){
            if(!result.success){
                FeedbackCtrl.instance.showError($glVars.i18n.tags.appName, result.msg);
            }
            else{
                FeedbackCtrl.instance.showInfo($glVars.i18n.tags.appName, $glVars.i18n.tags.msgSuccess, 3);
                that.getData();
            }
        };

        if(window.confirm($glVars.i18n.tags.msgConfirmClone)){
            $glVars.webApi.cloneTemplate(templateId, callback);
        }
    }
}

export class ModalTemplateForm extends Component{
    static defaultProps = {        
        templateId: 0,
        onClose: null,
        title: 'Crer un gabarit'
    };

    constructor(props){
        super(props);

        this.getData = this.getData.bind(this);
        this.getDataResult = this.getDataResult.bind(this);
        this.onSave = this.onSave.bind(this);
        this.onSaveTplAct = this.onSaveTplAct.bind(this);
        this.onRemoveTplAct = this.onRemoveTplAct.bind(this);
        this.onAddTplAct = this.onAddTplAct.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onDataChange = this.onDataChange.bind(this);
        this.onFilterChange = this.onFilterChange.bind(this);
        this.onDragRow = this.onDragRow.bind(this);
        this.onDropRow = this.onDropRow.bind(this);

        this.state = {data: null, draggingItem: null, dropdownLists: {categoryId: "0", categoryList: [], courseId: "0", courseList: [], activityList: []}, flags: {dataChanged: false, refresh: false}};
    }

    componentDidMount(){
        this.getData();
    }

    render(){
        if(this.state.data === null){ return null; }

        let tmpActivityList = this.state.dropdownLists.activityList.filter(item => (JsNx.getItem(this.state.data.activities, 'cmId', item.cmId, null) === null) );

        let tmpCourseList = this.state.dropdownLists.courseList.filter(item => (item.data.categoryId === this.state.dropdownLists.categoryId));

        let activities = this.state.data.activities.sort((item, item2) => { return item.slot - item2.slot });

        let body = 
            <div style={{display: 'grid', gridTemplateColumns: '48% 48%', gridGap: '2rem'}}>
                <div style={{backgroundColor: '#f9f9f9', padding: '1rem'}}>
                    <h6>Filtrez par catgorie et cours</h6>
                    <fieldset className="mb-3">
                        <Form.Row>
                            <Form.Group as={Col}>
                                <Form.Label>{"Catgorie"}</Form.Label>
                                <ComboBox placeholder={"Slectionnez votre option"} name="categoryId" value={this.state.dropdownLists.categoryId} options={this.state.dropdownLists.categoryList} onChange={this.onFilterChange} />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col}>
                                <Form.Label>{"Cours"}</Form.Label>
                                <ComboBox placeholder={"Slectionnez votre option"} name="courseId" value={this.state.dropdownLists.courseId} options={tmpCourseList} onChange={this.onFilterChange} />
                            </Form.Group>
                        </Form.Row>
                    </fieldset>
                    <div>
                        <h6>Liste d'activits <Badge variant="warning" className="p-2 rounded">{`${tmpActivityList.length}`}</Badge></h6>
                        <div style={{maxHeight: 500, overflowY: 'scroll'}}>
                            <Table striped bordered hover>                                
                                <thead>
                                    <tr>
                                        <th>Activit</th>
                                        <th style={{width: 70}}></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {tmpActivityList.map((item, index) => {
                                        let row =
                                            <tr key={index}>
                                                <td>{item.cmName}</td>
                                                <td style={{textAlign: 'center'}}><Button onClick={() => this.onAddTplAct(item)} size="sm" variant="primary" title="Ajouter" className="mr-2"><FontAwesomeIcon icon={faPlusSquare}/></Button></td>
                                            </tr>

                                            return row;
                                        }
                                    )}
                                </tbody>
                            </Table>
                        </div>
                    </div>
                </div>
                <div>
                    <Form noValidate validated={this.state.formValidated} ref={this.formRef}>
                        <Form.Row>
                            <Form.Group as={Col}>
                                <Form.Label>{"Nom"}</Form.Label>
                                <Form.Control type="text" value={this.state.data.name}  onBlur={() => this.onSave(this.state.data)} name="name" onChange={this.onDataChange} />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col}>
                                <Form.Label>{"Description"}</Form.Label>
                                <Form.Control as="textarea" rows={4}  value={this.state.data.description} onBlur={() => this.onSave(this.state.data)}  name="description" onChange={this.onDataChange} />
                            </Form.Group>
                        </Form.Row>
                        <div>
                            <h6>Activits slectionnes <Badge variant="warning" className="p-2 rounded">{`${this.state.data.activities.length}`}</Badge></h6>
                            <div style={{maxHeight: 500, overflowY: 'scroll'}}>
                                <DataGrid>
                                    <DataGrid.Header>
                                        <DataGrid.Header.Row>
                                            <DataGrid.Header.Cell></DataGrid.Header.Cell>
                                            <DataGrid.Header.Cell>#</DataGrid.Header.Cell>
                                            <DataGrid.Header.Cell>Cours</DataGrid.Header.Cell>
                                            <DataGrid.Header.Cell>Activit</DataGrid.Header.Cell>
                                            <DataGrid.Header.Cell>Temps en heure</DataGrid.Header.Cell>
                                            <DataGrid.Header.Cell></DataGrid.Header.Cell>
                                        </DataGrid.Header.Row>
                                    </DataGrid.Header>
                                    <DataGrid.Body>
                                        {activities.map((item, index) => {
                                                let row =
                                                    <DataGrid.Body.RowDraggable data={item} onDrag={this.onDragRow} onDrop={this.onDropRow} key={index}>
                                                    <DataGrid.Body.Cell><FontAwesomeIcon icon={faGripVertical} title="Dplacer l'item"/></DataGrid.Body.Cell>
                                                        <DataGrid.Body.Cell>{index + 1}</DataGrid.Body.Cell>
                                                        <DataGrid.Body.Cell>{item.courseName}</DataGrid.Body.Cell>
                                                        <DataGrid.Body.Cell>{item.cmName}</DataGrid.Body.Cell>
                                                        <DataGrid.Body.Cell><Form.Control type="text" placeholder="" value={item.nbHoursCompletion} onBlur={() => this.onSaveTplAct(item)} name="nbHoursCompletion" onChange={(event) => this.onDataChange(event, index)} /></DataGrid.Body.Cell>
                                                        <DataGrid.Body.Cell><Button size="sm" variant="primary" title="Supprimer" onClick={() => this.onRemoveTplAct(item.id)}><FontAwesomeIcon icon={faTrashAlt}/></Button></DataGrid.Body.Cell>
                                                    </DataGrid.Body.RowDraggable>;

                                                return row;
                                            }
                                        )}
                                    </DataGrid.Body>
                                </DataGrid>
                            </div>
                        </div>
                    </Form>
                </div>
            </div>;

        let main = <Modal title={this.props.title} body={body} onClose={this.onClose} />;

        return (main);
    }

    getData(){
        $glVars.webApi.getTemplateFormFormKit(this.props.templateId, this.getDataResult);
    }

    getDataResult(result){
        if(!result.success){
            FeedbackCtrl.instance.showError($glVars.i18n.tags.appName, result.msg);
            return;
        }

        let categoryList = [];
        for(let item of result.data.catCourseList){
            if(JsNx.getItem(categoryList, 'value', item.categoryId, null) === null){
                categoryList.push({text: item.categoryName, value: item.categoryId});
            }
        }

        let courseList = [];
        for(let item of result.data.catCourseList){
            if(JsNx.getItem(courseList, 'value', item.courseId, null) === null){
                courseList.push({text: item.courseName, value: item.courseId, data: item});
            }
        }

        this.setState({
            data: result.data.data, 
            dropdownLists: {categoryList: categoryList, courseList: courseList, activityList: []}
        });
    }

    onDragRow(item, index){
        this.setState({draggingItem: item});
    }

    onDropRow(item, index){
        let data = this.state.data;
        item = JsNx.getItem(data.activities, 'id', item.id, null);
        let draggingItem = JsNx.getItem(data.activities, 'id', this.state.draggingItem.id, null);
        let oldSlot = item.slot;
        item.slot = draggingItem.slot;
        draggingItem.slot = oldSlot;
        this.setState({flags: {dataChanged: true, refresh: true}}, () => {this.onSaveTplAct(item); this.onSaveTplAct(draggingItem)});
    }

    onAddTplAct(item){
        let newItem = {};
        newItem.id = 0;
        newItem.slot = this.state.data.activities.length + 1;
        newItem.cmId = item.cmId;
        newItem.cmName = item.cmName;
        newItem.courseName = item.courseName;
        newItem.templateId = this.state.data.id;
        newItem.nbHoursCompletion = 0;
        this.setState({flags: {dataChanged: true, refresh: true}}, () => this.onSaveTplAct(newItem));
    }

    onRemoveTplAct(tplActId){
        let that = this;
        let callback = function(result){
            if(!result.success){
                $glVars.feedback.showError($glVars.i18n.tags.appName, result.msg);
                return;
            }

            let data = that.state.data;
            JsNx.removeItem(data.activities, 'id', tplActId);
            that.setState({data: data, flags: {dataChanged: that.state.flags.dataChanged, refresh: true}});
        }

        if(window.confirm($glVars.i18n.tags.msgConfirmDeletion)){
            $glVars.webApi.deleteTplAct(tplActId, callback);
        }
    }

    onDataChange(event, index){
        index = (index >= 0 ? index : -1);
        let data = this.state.data;
        let flags = this.state.flags;

        if(index >= 0){
            flags.dataChanged = (data.activities[index][event.target.name] !== event.target.value);
            data.activities[index][event.target.name] = event.target.value;
        }
        else{
            flags.dataChanged = (data[event.target.name] !== event.target.value);
            data[event.target.name] = event.target.value;
        }

        flags.refresh = true;
        this.setState({data: data, flags: flags});
    }

    onFilterChange(event){
        let that = this;
        let item = this.state.dropdownLists;
        item[event.target.name] = event.target.value;

        if(event.target.name === "categoryId"){
            item.courseId = "0";
            item.activityList = [];
        }

        if(item.courseId !== "0"){
            $glVars.webApi.getCatCourseSectionActivityList(true, item.categoryId, item.courseId, (result) => {
                item.activityList = result.data;
                that.setState({dropdownLists: item});
            });
        }
        else{
            this.setState({dropdownLists: item});
        }
    }
    
    onSave(data){
        let that = this;
        let callback = function(result){
            if(!result.success){
                $glVars.feedback.showError($glVars.i18n.tags.appName, result.msg);
                return;
            }

            data.id = result.data;
            that.setState({data: data, flags: {dataChanged: false, refresh: that.state.flags.refresh}});
        }

        if(this.state.flags.dataChanged){
            $glVars.webApi.saveTemplate(data, callback);
        }
    }

    onSaveTplAct(tplAct){
        let that = this;
        let callback = function(result){
            if(!result.success){
                $glVars.feedback.showError($glVars.i18n.tags.appName, result.msg);
                return;
            }

            let data = that.state.data;

            if(tplAct.id === 0){
                tplAct.id = result.data.tplActId;
                data.activities.push(tplAct);
            }
            
            if(data.id === 0){
                data.id = result.data.templateId;
            }

            that.setState({data: data, flags: {dataChanged: false, refresh: that.state.flags.refresh}});
        }

        if(this.state.flags.dataChanged){
            $glVars.webApi.saveTplAct({templateId: this.state.data.id, id: tplAct.id, cmId: tplAct.cmId, nbHoursCompletion: tplAct.nbHoursCompletion, slot: tplAct.slot}, callback);
        }
    }

    onClose(){
        this.props.onClose(this.state.flags.refresh);
    }
}